@import './Constants.css';

.background-spotlight-button{
    --mouse-x: 0px;
    --mouse-y: 0px;
    --spotlight-opacity: 0;
    position: relative;
    z-index: 1;
    
    height: 40px;
    aspect-ratio: 2 / 1;
    border-radius: 10px;
    
    cursor: pointer;

    pointer-events: all;
    /*For centering text children*/
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    

}

.background-spotlight-button-shadow{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    
    border-radius: inherit;
    box-shadow: 
        0px 0px 10px var(--blue-300),
        0px 0px 3px var(--blue-100);
    transition: box-shadow 0.3s ease-in-out 0s, opacity 0.3s ease-in-out 0s;
    
    opacity: 0;
}

.background-spotlight-button:hover > .background-spotlight-button-shadow{
    box-shadow: 
        0px 0px 15px var(--blue-700),
        0px 0px 5px var(--blue-300);
    opacity: 0.3;
}

.background-spotlight-button::before,
.background-spotlight-button::after{
    --mouse-x: inherit;
    --mouse-y: inherit;
    --spotlight-opacity: inherit;
    border-radius: inherit;

    content: '';
    position: absolute;
}

.background-spotlight-button::after{
    z-index: 2;
    inset: 1px;
    position: absolute;
    pointer-events: none;

    background-image: 
        radial-gradient(40px circle at var(--mouse-x) var(--mouse-y), rgba(248,250,252, calc(0.15 * var(--spotlight-opacity))) 0%, transparent 100%),
        linear-gradient(0deg, var(--slate-950) 0%, var(--slate-900) 100%);
    
}

.background-spotlight-button:hover::after{
    opacity: 1;
}

.background-spotlight-button::before{
    z-index: -1;
    bottom: 0px;
    right: 0px;
    top: 0px;
    left: 0px;

    background-image: 
        radial-gradient(45px circle at var(--mouse-x) var(--mouse-y), rgba(248,250,252, calc(0.5 * var(--spotlight-opacity))) 0%, transparent 100%);
    background-color: var(--slate-700);
}

.background-spotlight-button:hover > *{
    color: var(--white1);
    text-shadow: 0px 0px 3px rgba(255,255,255, 0.4);
}

.background-spotlight-button > *:not(.background-spotlight-button-shadow){
    position: relative;
    font-family: var(--mono);
    text-align: center;
    z-index: 3;

    color: var(--white2);
    transition: color 0.5s ease-in-out;
    font-size: var(--default-font-size);
    text-shadow: 0px 0px 2px rgba(255,255,255, 00);

    transition: color 0.5s ease, text-shadow 0.5s ease;
}